@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@400;600;700&family=Open+Sans:wght@300;400;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{font-family:"Open Sans", sans-serif !important;background-color:#2e2e35 !important;color:#dadada;scroll-behavior:smooth}h1 a,h2 a,h3 a,h4 a,h5 a,h6 a{color:inherit}h1{font-size:50px;line-height:60px;font-weight:800}h2{font-size:35px;line-height:45px;color:#dadada;font-weight:700}h3{font-weight:700;font-size:1.75rem}h4{font-size:20px;line-height:30px;font-weight:700}h5{font-size:18px;font-weight:700}h6{font-size:16px;font-weight:700}p{font-size:16px;font-weight:normal;line-height:26px;color:#dadada;margin-bottom:20px;font-family:"Nunito", sans-serif}.text-secondary-color{color:#e4aa48}a{color:#b8b7b7;font-weight:700;text-decoration:none;cursor:pointer}a:hover{color:#e4aa48;text-decoration:none}a:focus,a:hover,.portfolio-cat a:hover,.footer -menu li a:hover{color:#e4aa48;text-decoration:none}li{list-style:none}select{padding:8px 16px;margin:12px;background:#5e5e5e;outline:0;border-radius:24px;font-size:16px;font-weight:700;color:#fff}::selection{background:#fff;text-shadow:none}*::-webkit-input-placeholder{color:#949494;font-size:26px;text-align:center}*:-ms-input-placeholder{color:#949494;font-size:26px;text-align:center}*::placeholder{color:#b5b5b5;font-size:16px;opacity:1;text-align:left}*::focus{outline:none}*::active{outline:none}*::-moz-focus{outline:none}

img{max-width:100%;transition:all 0.3s ease-out 0s;height:auto}.horizontal-dash{width:100%;height:3px;background:#e4aa48}.custom-btn{background-color:transparent;text-transform:uppercase;font-family:"Nunito", sans-serif;font-size:14px;color:#e4aa48;border:2px solid #e4aa48;width:150px;height:50px;line-height:46px;letter-spacing:0.12em;text-align:center;display:inline-block;cursor:pointer}.custom-btn:hover{background:#e4aa48;color:#000;transition:0.3s ease-out}section{min-height:100vh;margin:0 !important}@media (max-width: 1280px){section{min-height:unset;max-height:-webkit-max-content;max-height:max-content}}section:nth-of-type(n){background-color:#2e2e35}section:nth-of-type(2n){background-color:#34343b}

#landing_page nav .logo{height:55px;width:55px}#landing_page nav .menu a{color:#dadada}#landing_page nav .menu a:hover::after{-webkit-transform:scaleX(1);transform:scaleX(1);-webkit-transform-origin:left;transform-origin:left}#landing_page nav .menu a::after{position:absolute;content:"";top:110%;left:0;width:100%;height:3px;background:#e4aa48;-webkit-transform:scaleX(0);transform:scaleX(0);transition:-webkit-transform 0.5s;transition:transform 0.5s;transition:transform 0.5s, -webkit-transform 0.5s;-webkit-transform-origin:right;transform-origin:right}#landing_page nav .menu>a{margin-right:16px;padding-left:0.5rem;padding-right:0.5rem;cursor:pointer;position:relative;display:block}#landing_page nav .social{border-left:2px solid #737377}#landing_page nav .social li{margin-right:16px;position:relative}#landing_page nav .social li:hover:after{margin:3px auto;display:block}#landing_page nav .social li:first-child{margin-left:15px}#landing_page nav .social li:last-child{margin-right:15px !important}#landing_page nav .social a svg{height:23px;width:23px;margin-bottom:5px;fill:#b8b7b7}#landing_page nav .social a:hover svg{fill:#e4aa48}#landing_page .landing_page__main .left .horizontal-dash{margin-top:31px;margin-bottom:14px;width:100px}@media (min-width: 1200px){#landing_page .landing_page__main .left{flex:0 0 25%;max-width:25%}}@media (max-width: 768px){#landing_page .landing_page__main .left{display:inline-flex;flex-direction:column;align-items:center;flex-grow:1}}@media (min-width: 640px) and (max-width: 1200px){#landing_page .landing_page__main .right{display:none}}@media (min-width: 1200px){#landing_page .landing_page__main .right{flex:0 0 30%;max-width:30%}}

